import React from 'react';
import { useStaticQuery, graphql } from 'gatsby';
import Tag from './tags';

const Sidebar = function () {
    const { tags } = useStaticQuery(graphql`
        query MyQuery {
            tags: codemasterJson {
                tags
            }
        }
    `);
    return (
        <div className="space-y-8 bg-gray-800 p-6 rounded">
            <div className="space-y-2">
                <h2 className="font-semibold tracking-widest uppercase dark:text-coolGray-400">
                    Search Courses
                </h2>
                <div className="aside-search flex">
                    <input
                        v-model="input"
                        className="py-2 px-2 w-full border rounded"
                        type="text"
                        placeholder="Search Here..."
                    />
                    <button type="button" className="block rounded ml-2 px-4 bg-green-400">
                        <svg
                            xmlns="http://www.w3.org/2000/svg"
                            viewBox="-2.5 -2.5 24 24"
                            width="24"
                            fill="currentColor"
                        >
                            <path d="M8 14A6 6 0 1 0 8 2a6 6 0 0 0 0 12zm6.32-1.094l3.58 3.58a1 1 0 1 1-1.415 1.413l-3.58-3.58a8 8 0 1 1 1.414-1.414z" />
                        </svg>
                    </button>
                </div>
            </div>
            <div className="space-y-2">
                <h2 className="font-semibold tracking-widest uppercase mb-4">Tags</h2>
                <div className="flex flex-col space-y-4 font-normal">
                    {tags.tags.map((tag) => (
                        <Tag
                            routePath={tag.toLowerCase().split(' ').join('-')}
                            key={tag.toLowerCase().split(' ').join('-')}
                        >
                            {tag}
                        </Tag>
                    ))}
                </div>
            </div>
        </div>
    );
};

export default Sidebar;
