import React from 'react';
import Header from 'components/header';
import Footer from 'components/footer';
import Navigation from 'components/navigation';

import { isMobile } from 'react-device-detect';

import Subscribe from 'components/codemaster/subscribe';
import SearchBox from 'components/codemaster/search-box';
import WithSiderbarMobile from './mobile/with-sidebar';
import WithSiderbarDesktop from './desktop/with-sidebar';

export default function ({ children }) {
    return (
        <>
            <Header />
            <Navigation />
            <SearchBox />
            {children}
            <div className="mt-4" />
            <Subscribe />
            <Footer />
        </>
    );
}

export const WithSideBar = function ({ children }) {
    return isMobile ? (
        <WithSiderbarMobile>{children}</WithSiderbarMobile>
    ) : (
        <WithSiderbarDesktop>{children}</WithSiderbarDesktop>
    );
};
