import React from 'react';
import { Link, useStaticQuery, graphql } from 'gatsby';
import { isMobileOnly } from 'react-device-detect';
// eslint-disable-next-line import/no-unresolved
import Humbar from 'assets/hum-bar.svg';
// eslint-disable-next-line import/no-unresolved
import CloseBtn from 'assets/close.svg';
import Tag from 'components/codemaster/tags';
import { useLocation } from '@reach/router';

const Navigation = function () {
    const routeLocation = useLocation();

    const [toggle, setToggle] = React.useState(false);
    const navRef = React.useRef(null);
    const [fromTop, setFromTop] = React.useState(null);

    const { categories, tags } = useStaticQuery(graphql`
        query {
            categories: allCategoriesJson {
                edges {
                    node {
                        name
                        routerPath
                    }
                }
            }
            tags: codemasterJson {
                tags
            }
        }
    `);
    React.useEffect(() => {
        if (navRef.current) {
            setFromTop(navRef.current.getBoundingClientRect().top);
        }
    }, []);
    React.useEffect(() => {
        if (toggle) document.body.style.overflow = 'hidden';
        else document.body.style.overflow = 'auto';
    }, [toggle]);
    React.useEffect(() => {
        setToggle(false);
    }, [routeLocation.pathname]);
    return (
        <nav
            ref={navRef}
            className="container relative mx-auto px-4 bg-gray-800 rounded justify-between flex my-3 font-bold"
        >
            <ul className="flex py-2 relative w-full items-center">
                <Link className="py-2 px-3 inline-block hover:bg-gray-900 rounded" to="/">
                    Home
                </Link>
                {isMobileOnly ? (
                    <div className="flex items-center ml-auto">
                        {toggle && (
                            <>
                                <div
                                    onClick={() => setToggle(false)}
                                    className="w-screen h-screen bg-gray-400 fixed top-0 left-0 opacity-30 z-10"
                                />
                                <div
                                    className="fixed py-2 right-0 bg-gray-800 rounded w-64 z-20 shadow-md"
                                    style={{
                                        height: `calc(100vh - ${fromTop || 100}px)`,
                                        top: `${fromTop}px`
                                    }}
                                >
                                    <button
                                        onClick={() => setToggle(false)}
                                        type="button"
                                        className="transform -rotate-90 block p-3 bg-gray-900 rounded ml-auto mr-4"
                                    >
                                        <CloseBtn className="text-white w-6 h-6 inline-block" />
                                    </button>
                                    <div
                                        className="px-4 py-2 my-2 overflow-auto"
                                        style={{ height: '95%' }}
                                    >
                                        <p className="mb-3">Categories: </p>
                                        {categories.edges.map(({ node }) => (
                                            <Link
                                                key={node.name}
                                                className="py-2 px-3 block hover:bg-gray-900 rounded"
                                                to={`/categories/${node.routerPath}`}
                                            >
                                                {node.name}
                                            </Link>
                                        ))}
                                        <p className="mb-3 mt-3">Tags: </p>
                                        <div className="flex flex-col space-y-4 font-normal">
                                            {tags.tags.map((tag) => (
                                                <Tag
                                                    routePath={tag
                                                        .toLowerCase()
                                                        .split(' ')
                                                        .join('-')}
                                                    key={tag.toLowerCase().split(' ').join('-')}
                                                >
                                                    {tag}
                                                </Tag>
                                            ))}
                                        </div>
                                        <div className="mt-8" />
                                    </div>
                                </div>
                            </>
                        )}

                        <button
                            onClick={() => setToggle(true)}
                            type="button"
                            className="transform -rotate-90"
                        >
                            <Humbar className="text-white w-6 h-6 inline-block" />
                        </button>
                    </div>
                ) : (
                    categories.edges.map(({ node }) => (
                        <Link
                            key={node.name}
                            className="py-2 px-3 inline-block hover:bg-gray-900 rounded"
                            to={`/categories/${node.routerPath}`}
                        >
                            {node.name}
                        </Link>
                    ))
                )}
            </ul>
            {/* <button type="button">
                <svg
                    xmlns="http://www.w3.org/2000/svg"
                    viewBox="-2.5 -2.5 24 24"
                    width="24"
                    fill="currentColor"
                >
                    <path d="M8 14A6 6 0 1 0 8 2a6 6 0 0 0 0 12zm6.32-1.094l3.58 3.58a1 1 0 1 1-1.415 1.413l-3.58-3.58a8 8 0 1 1 1.414-1.414z" />
                </svg>
            </button> */}
        </nav>
    );
};

export default Navigation;
