import React from 'react'
import algoliasearch from 'algoliasearch/lite';
import { InstantSearch, SearchBox, connectHits } from 'react-instantsearch-dom';
import { Link } from 'gatsby'

const searchClient = algoliasearch(process.env.GATSBY_ALGOLIA_APP_ID, process.env.GATSBY_ALGOLIA_SEARCH_KEY);

const Hits = connectHits(({ hits }) => (
    <div>
        {hits.length ? (
            <div className="post-feed">
                {hits.map((hit) => (
                    <article key={hit.objectID} className="post post-card">
                        <div className="post-card-inside">
                            {/* <Link className="post-card-thumbnail" to={safePrefix(hit.fields.slug)}>
                                    <img className="thumbnail" src={safePrefix(hit.thumb_image)} alt={hit.title} />
                                </Link> */}
                            <div className="post-card-content">
                                <header className="post-header">
                                    <h2 className="post-title">
                                        <Link to={`/${hit.slug}`} rel="bookmark">
                                            {hit.title}
                                        </Link>
                                    </h2>
                                </header>
                                <div className="post-excerpt">
                                    <p>{hit.description}</p>
                                </div>
                            </div>
                        </div>
                    </article>
                ))}
            </div>
        ) : (
            <p>There were no results for your query. Please try again.</p>
        )}
    </div>
));

export default function Search() {
    return (
        <div className="outer">
            <div className="inner">
                <InstantSearch searchClient={searchClient} indexName="courses">
                    <SearchBox />
                    <Hits />
                </InstantSearch>
            </div>
        </div>
    );

}